*{
  margin: 0 0;
  padding: 0 0;
}
body{
  overflow-x: hidden;
}
.gridBlock{
  width: 20px;
  height: 20px;
  display: inline-flex;
  border: 0.5px solid rgb(0, 0, 0);
  text-align: center;
  justify-content: center;
  align-items: center;
}
nav{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100vw;
  padding: 6px 0;
  font-size: 22px;
  background-color: rgba(128, 128, 128, 0.466);
}
nav p {
  cursor: pointer;
}
.navItem{
  display: flex;
  width: 35vw;
  justify-content: space-around;
}
.pauseButtons{
  font-size: 35px;
  cursor: pointer;
  display: flex;
}
.gencounter{
  font-size: 20px;
  display: flex;

}
.gencounter p{
  padding: 5px;
}
#myCanvas{
  border: 1px solid black;
  width: 900px;
  height: 900px;
  
}
.canvasWrap{
  display: flex;
  justify-content: center;
  margin: 20px 0;
}
.szabalydiv{
  display: flex;

}
.szabalydiv p{
  padding: 3px 6px;
}